<template lang="html">
  <v-app>
    <v-container class="mainContainer margtop">
      <v-layout row class="pic mt-3" v-for="album in albums">
        <v-flex xs12>
          <img :src="album.imgURL">
          <h5 class="text-xs-center">{{album.text}}</h5>
        </v-flex>
      </v-layout>
    </v-container>
  </v-app>
</template>

<script>
export default {
  data() {
    return {
      albums: [{
          imgURL: '../static/album_1/pic_1.jpg',
          text: 'Pic_1 Title',
        },
        {
          imgURL: '../static/album_1/pic_2.jpg',
          text: 'Pic_2 Title',
        },
        {
          imgURL: '../static/album_1/pic_3.jpg',
          text: 'Pic_3 Title',
        },
        {
          imgURL: '../static/album_1/pic_4.jpg',
          text: 'Pic_4 Title',
        },
        {
          imgURL: '../static/album_1/pic_5.jpg',
          text: 'Pic_5 Title',
        },
        {
          imgURL: '../static/album_1/pic_6.jpg',
          text: 'Pic_6 Title',
        }
      ]

    }
  }
}
</script>

<style scoped lang="css">
  .pic{

  }
</style>



