import Home from '../components/home.vue'
import album_1 from '../components/album_1.vue'
import contact from '../components/contact.vue'

export default [{
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/album_1',
    name: 'album_1',
    component: album_1
  },
  {
    path: '/contact',
    name: 'contact',
    component: contact
  }
]



