<template>
<v-app>
  <v-toolbar fixed flat class="mainToolbar">
    <v-container class="mainContainer2">
      <v-layout class="rowz">
        <v-flex xs6>
          <router-link class="titleMain pl-3" to="/">
            Milan Petrovic
          </router-link>
        </v-flex>
        <v-flex xs6 class="text-xs-right">
          <router-link class="contact pr-3" to="/contact">
            Contact
          </router-link>
        </v-flex>
      </v-layout>
    </v-container>
  </v-toolbar>
  <v-container class="mainContainer2 margtop" grid-list-lg>
    <v-layout row wrap class="text-xs-center">
      <v-flex xs12 md6 v-for="album in albums">
        <router-link to="/album_1">
          <img :src="album.imgURL">
        </router-link>
        <h5 class="text-xs-center">{{album.text}}</h5>
      </v-flex>
    </v-layout>
  </v-container>
</v-app>
</template>
<script>
export default {
  data() {
    return {
      albums: [{
          imgURL: '../static/album_1/thumbnail_1.jpg',
          text: 'album_1',
          link: '/album_1'
        },
        {
          imgURL: './static/600.png',
          text: 'album_2',
          link: '/album_2'
        },
        {
          imgURL: './static/600.png',
          text: 'album_3',
          link: '/album_3'
        },
        {
          imgURL: './static/600.png',
          text: 'album_4',
          link: '/album_4'
        },
        {
          imgURL: './static/600.png',
          text: 'album_5',
          link: '/album_5'
        },
        {
          imgURL: './static/600.png',
          text: 'album_6',
          link: '/album_6'
        }
      ]

    }
  }
}
</script>
<style>
img {
  max-width: 100%;
  height: auto;
}

.mainContainer {
  max-width: 1500px;
  padding: 0;
}

.mainContainer2 {
  max-width: 1300px;
}

.mainToolbar {
  height: 120px;
  background-color: #fcfcfc !important;
  opacity: 0.9;
}

.rowz {
  line-height: 120px;
}

.titleMain,
.contact {
  cursor: pointer;
  text-decoration: none;
  color: black;
}

.margtop {
  margin-top: 120px;
}

.fade-enter-active {
  transition: 1s ease-in-out;
}

.fade-leave-to,
.fade-enter {
  transform: translatey(-2000px);
}
</style>



